//定时任务
import http from '../httpConfig'

const table = 'mrbase_sys_params_setting'
//获取所有系统参数
export const getSystemParameterList = (params = {}) => {
  return new Promise((resolve, reject) => {
    http.post('/api/systemParameter/getAll/v1', params)
      .then(res => {
        resolve(res)
      })
      .catch(err => {
        reject(err)
      })
  })
}

//获取系统参数详情
