<template>
  <div>
    <mrc_ess_table v-if="$route.name=='codeLogic'"
                   :headers='headers'
                   pageName='addCodeLogic'
                   :paginationOptions='options'
                   @click:row='godetail'
                   @turnPage='getlist'
                   :slotKey="['status']"
                   :isLoading='loading'
                   :items='desserts'>

      <!-- 状态 -->
      <template #item.status='{status}'>
        <v-btn rounded
               color="#13a671"
               dark
               x-smalls
               v-if="status==0">正常</v-btn>
        <v-btn rounded
               color="#999999"
               dark
               x-small
               v-if="status==1">暂停</v-btn>
      </template>

      <!-- 操作 -->
      <template v-slot:item.actions='{item}'>
        <menu-list :list='menulist'
                   @handleClick='handleClick($event,item)'>
          <template v-slot:showAction>
            <v-icon>mdi-dots-vertical</v-icon>
          </template>
        </menu-list>
      </template>
    </mrc_ess_table>
    <router-view v-else></router-view>
  </div>
</template>

<script>
import {
  getSystemParameterList, detailSystemParameter
  , addSystemParameter, updateSystemParameter, deleteSystemParameter
} from '@/service/api/systemParameter.js'
export default {
  components: {

  },
  props: {},
  data () {
    return {
      disabled: true,
      list: {},
      options: {
        pagesize: 5,//条数
        cpage: this.$store.state.cpage,  //页数
        totalCount: null, //总条数
        totalpage: null, //总页数
      },
      loading: false,
      menulist: [
        { name: this.$t('label_Edit') },
        { name: this.$t('label_delete') },
      ],
      headers: [
        { text: 'Name&Method', align: 'start', value: 'param_name', sortable: false },
        { text: this.$t('label_Change_the_table'), value: 'param_key', sortable: false },
        { text: '', value: 'status', sortable: false, },
        { text: '', value: 'actions', sortable: false, align: 'end', },
      ],
      desserts: [],
    };
  },
  beforeDestroy () {
    this.$store.commit('setCpage', 1)
  },
  computed: {},
  created () { },
  watch: {
    $route (v) {
      if (v.path == '/codeLogic') {
        this.getlist()
      }
    }
  },
  async mounted () {
    await this.getlist()
  },

  methods: {
    async getlist (obj) {
      this.loading = true
      if (this.desserts.length == 1 && this.options.cpage != 1) this.options.cpage--
      if (obj) {
        const { cpage, pagesize } = obj
        this.options = obj
      }
      const res = await getSystemParameterList({
        cpage: this.options.cpage, pagesize: this.options.pagesize
      })
      if (res.code == 200) {
        this.desserts = res.data
        this.options.totalCount = res.totalCount
        this.options.totalpage = res.totalpage
      }
      this.loading = false
    },
    godetail (item) {
      this.$store.commit('set_iconstatus', true)  //行内进图标显示
      this.$store.commit('set_subPage_name', item.param_name)
      this.$store.commit('set_uuid', item.uuid)  //
      this.$router.push({
        name: 'detailCodeLogic'
      })
    },
    handleClick ({ index }, item) {
      switch (index) {
        case 0:
          this.$store.commit('set_subPage_name', item.param_name)
          this.$store.commit('set_iconstatus', false)  //行内进图标显示 
          this.$store.commit('set_uuid', item.uuid)  //
          this.$router.push({
            name: 'detailCodeLogic'
          })
          break
        case 1:

          this.$myConfirm({ content: this.$t('message_Confirm_the_deletion') }).then(res => {
            deleteSystemParameter({ uuid: item.uuid }).then(res => {
              if (res.code === '200') {
                this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
                this.getlist()
              }
            })
          })
          break
      }
    },
    addemailtemplate () {
      this.$store.commit('set_subPage_name', '')
      this.$store.commit('set_uuid', '')
      this.$store.commit('set_iconstatus', '')  //行内进图标显示
      this.$router.push({
        name: 'addCodeLogic'
      })
    }
  },
};
</script>

<style scoped>
.cardtitle {
  position: relative;
}
.emailbtn {
  position: absolute;
  right: 70px;
  top: -50px;
}
</style>
